/*
 * ## Media Queries
 */
.mm-assessment__questions {}
.mm-assessment__heading {
  text-transform: uppercase;
}
.mm-assessment__results--hidden,.mm-assessment__results--loading {
  display: none;
}
.mm-assessment__form {}
.mm-assessment__input-group {}
.mm-assessment__label {}
.mm-assessment__input--text {}
.mm-assessment__input--email {}
.mm-assessment__button {}
/*
 * Kind of hacky, but it seems more relevant/debuggable here than in the reset.
 */
body {
  counter-reset: questions;
}
.mm-question {
  padding: 30px;
  border: 3px solid #d7ced5;
  opacity: 0.8;
  -webkit-transition: 200ms all linear;
          transition: 200ms all linear;
}
.mm-question:first-child:not(.mm-question--answered),.mm-question--answered+.mm-question:not(.mm-question--answered) {
  background: rgb(251, 250, 251);
  background: -webkit-linear-gradient(top, rgb(253, 253, 253) 0%, rgb(245, 243, 245) 100%);
  background: linear-gradient(
      to bottom,
      rgb(253, 253, 253) 0%,
      rgb(245, 243, 245) 100%
    );
  border-color: #640035;
  box-shadow: 0 0 10px rgba(100, 0, 53, 0.5);
  opacity: 1;
}
.mm-question--answered {
  opacity: 0.5;
}
.mm-question--answered:hover {
  opacity: 1;
}
.mm-question h3.mm-question__heading {
  margin-top: 0;
  font-weight: 300;
  letter-spacing: 0;
  text-transform: none;
}
.mm-question h3.mm-question__heading::before {
  counter-increment: questions;
  content: 'Question ' counter(questions) ":";
  display: block;
  height: 2em;
  color: #640035;
  font-size: 50%;
  font-weight: 800;
  line-height: 2em;
  letter-spacing: 0.15em;
  text-align: center;
  text-transform: uppercase;
}
.mm-question__instructions {
  margin-top: 7.5px;
  font-size: 80%;
}
.mm-question__radio-group {
  list-style: none;
  padding: 0;
}
@media (min-width: 768px) {
  .mm-question__radio-group {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
  }
}
.mm-question__radio-group::before {
  content: 'Choose an option:';
  display: block;
  width: 100%;
  height: 2em;
  color: #640035;
  font-size: 50%;
  font-weight: 800;
  line-height: 2em;
  letter-spacing: 0.15em;
  text-align: center;
  text-transform: uppercase;
}
.mm-question__radio-item {
  position: relative;
  display: block;
  margin: 7.5px 0 0;
}
@media (min-width: 768px) {
  .mm-question__radio-item {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    margin: 0 1% 0 0;
  }
}
.mm-question__radio-item:last-of-type {
  margin-right: 0;
}
.mm-question__radio-item label {
  position: relative;
  display: block;
  width: 100%;
  margin: 0;
  padding: 7.5px;
  background-color: #fff;
  border: 2px solid #162329;
  border-radius: 7.5px;
  color: #162329;
  cursor: pointer;
  font-family: brandon-grotesque,sans-serif;
  font-weight: 700;
  font-size: 62.5%;
  line-height: 1.1;
  text-align: center;
  text-decoration: none;
  -webkit-transition: 200ms all ease-in-out;
          transition: 200ms all ease-in-out;
  z-index: 2;
}
.mm-question__radio-item label:hover,.mm-question__radio-item label:active,.mm-question__radio-item label:focus {
  outline: none;
  background: rgb(216, 191, 205);
  box-shadow: 0 0 10px rgba(100, 0, 53, 0.25);
  text-shadow: 0 0 5px rgba(100, 0, 53, 0.15);
}
.mm-question__input--radio {
  position: absolute;
  top: 10px;
  left: 10px;
  width: 1px;
  height: 1px;
  z-index: 1;
}
.mm-question__input--radio:checked+label {
  background: rgb(139, 64, 104);
  color: #fff;
}
.mm-question__label {}
.mm-results {
  position: relative;
  background: #fff;
  border: 6px solid #640035;
  box-shadow: 0 2px 8px rgba(22, 35, 41, 0.4);
}
.mm-results__header {
  position: relative;
  height: 300px;
  padding: 15px;
  overflow: hidden;
}
@media (min-width: 750px) {
  .mm-results__header {
    height: 265px;
  }
}
.mm-results__heading:first-child {
  position: relative;
  padding: 30px 30px 0;
  color: #fff;
  font-size: 125%;
  font-weight: 800;
  letter-spacing: 0.2em;
  text-align: center;
  text-shadow: 0 0 10px rgba(22, 35, 41, 0.4);
  z-index: 2;
}
@media (min-width: 750px) {
  .mm-results__heading:first-child {
    padding-top: 60px;
    padding-left: 15px;
    padding-right: 15px;
    font-size: 135%;
  }
}
.mm-results__grade {
  font-weight: inherit;
}
h3.mm-results__sub-heading {
  position: relative;
  margin-top: 7.5px;
  padding: 0 30px;
  color: #fff;
  font-size: 110%;
  text-align: center;
  text-shadow: 0 0 10px rgba(22, 35, 41, 0.4);
  z-index: 2;
}
@media (min-width: 750px) {
  h3.mm-results__sub-heading {
    padding-left: 15px;
    padding-right: 15px;
  }
}
.mm-results__image-wrap {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 90%;
  margin: 0 5%;
  padding: 15px 0 0;
  overflow: hidden;
  z-index: 1;
}
.mm-results__image-wrap::after {
  content: '';
  position: absolute;
  top: 15px;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(100, 0, 53, 0.75);
  z-index: 1;
}
@media (min-width: 750px) {
  .mm-results__image-wrap {
    width: 100%;
    margin: 0;
    padding: 15px 15px 0;
  }
  .mm-results__image-wrap::after {
    left: 15px;
    right: 15px;
  }
}
.mm-results__image {
  position: relative;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  display: block;
  width: auto;
  height: 100%;
}
@media (min-width: 750px) {
  .mm-results__image {
    position: static;
    left: auto;
    -webkit-transform: none;
            transform: none;
    width: 100%;
    height: auto;
  }
}
.mm-results__image-caption {
  position: absolute;
  bottom: 0;
  left: 5px;
  right: 5px;
  padding-bottom: 4px;
  padding-left: 7.5px;
  padding-right: 7.5px;
  color: #d7ced5;
  font-size: 10px;
  font-weight: 700;
  letter-spacing: 0.2em;
  line-height: 1;
  text-transform: uppercase;
  z-index: 2;
}
@media (min-width: 750px) {
  .mm-results__image-caption {
    left: 15px;
    right: 15px;
    padding-left: 15px;
    padding-right: 15px;
  }
}
.mm-results__image-credit {
  display: block;
  font-size: 10px;
}
@media (min-width: 750px) {
  .mm-results__image-credit {
    float: right;
  }
}
.mm-results__image-credit--link,.mm-results__image-credit a {
  color: inherit;
  text-decoration: none;
}
.mm-results__description {
  padding: 15px 30px;
}
.mm-results__form {
  padding: 0 5% 15px;
}
@media (min-width: 768px) {
  .mm-results__form {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: end;
        -ms-flex-align: end;
            align-items: flex-end;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    padding-left: 30px;
    padding-right: 30px;
  }
}
.mm-results__input-group {
  position: relative;
  display: block;
}
@media (min-width: 768px) {
  .mm-results__input-group {
    -webkit-box-flex: 1;
        -ms-flex: 1 30%;
            flex: 1 30%;
    max-width: 32%;
    margin-top: 0;
  }
}
.mm-results__label {
  position: absolute;
  top: 0;
  left: 0;
  color: #640035;
  font-size: 8px;
  font-weight: 700;
  letter-spacing: 0.2em;
  text-transform: uppercase;
}
.mm-results__input {
  display: block;
  width: 100%;
  padding: 0.75em 0.5em 0.25em;
  background: rgba(215, 206, 213, 0.5);
  border: 0;
  border-bottom: 2px solid #162329;
  color: #162329;
  font-family: brandon-grotesque,sans-serif;
  font-size: 16px;
  font-weight: 300;
  letter-spacing: 0.1em;
  line-height: 1.5;
  -webkit-transition: 200ms all ease-in-out;
          transition: 200ms all ease-in-out;
}
@media (min-width: 768px) {
  .mm-results__input {
    padding-top: 0.25em;
  }
}
.mm-results__input::-webkit-input-placeholder {
  color: inherit;
  text-shadow: 0 0 4px rgba(22, 35, 41, 0.4);
}
.mm-results__input::-moz-placeholder {
  color: inherit;
  text-shadow: 0 0 4px rgba(22, 35, 41, 0.4);
}
.mm-results__input:-ms-input-placeholder {
  color: inherit;
  text-shadow: 0 0 4px rgba(22, 35, 41, 0.4);
}
.mm-results__input::placeholder {
  color: inherit;
  text-shadow: 0 0 4px rgba(22, 35, 41, 0.4);
}
.mm-results__input:focus,.mm-results__input:hover {
  outline: none;
  background-color: rgba(22, 35, 41, 0.25);
  border-color: #640035;
}
.mm-results__button {
  display: block;
  width: 100%;
  font-size: 18px;
}
@media (min-width: 768px) {
  .mm-results__button {
    -webkit-box-flex: 1;
        -ms-flex: 1 30%;
            flex: 1 30%;
    max-width: 30%;
    margin-top: 0;
    font-size: 15px;
  }
}
.mm-results__notices-text {
  font-size: 10px;
  line-height: 1.25;
  text-align: center;
}
@media (min-width: 768px) {
  .mm-results__notices-text {
    -webkit-box-flex: 2;
        -ms-flex: 2 100%;
            flex: 2 100%;
    width: 100%;
    padding: 0 2em;
    font-size: 12px;
  }
}
